@import"https://fonts.googleapis.com/css?family=Kumbh+Sans";@import"https://fonts.googleapis.com/css?family=Roboto+Slab";@import"https://fonts.googleapis.com/css?family=Space+Mono";#settingsOverlay{display:none;position:fixed;width:100%;height:100%;top:0;left:0;background-color:rgba(0,0,0,.4);justify-content:center;align-items:center}#settingsWindow{display:flex;width:541px;height:464px;padding-left:20px;padding-right:20px;border-radius:30px;background-color:#fff;flex-flow:column;justify-content:space-around}@media only screen and (max-width: 500px){#settingsWindow{width:310px;height:530px}}#settingsHeader{display:flex;margin-top:15px;padding:0 10px 20px 10px;border-bottom:1px solid gray;justify-content:space-between}#settingsLabel{font-size:30px;color:#000}#closeSettingsButton{margin:0;padding:0;border:none;outline:none;background:none;cursor:pointer}.settingsSectionName{margin-top:16px;letter-spacing:5px;font-weight:bold}.settingsSection{display:flex;padding-top:20px;padding-bottom:25px;border-bottom:1px solid gray;flex-flow:row wrap;justify-content:space-between;align-items:center}.settingsSection:last-of-type{border-bottom:none}.timeSetter{display:flex;flex-flow:column nowrap}@media only screen and (max-width: 500px){.timeSetter{width:100%;flex-direction:row;justify-content:space-between;align-content:space-around;align-items:center;margin-bottom:10px}.timeSetter:last-of-type{margin-bottom:0px}}.inputTimerLabel{color:#a5a6b2;font-weight:bold}.inputTimerBox{display:flex;width:140px;height:50px;border-radius:10px;background-color:#eff1fa;justify-content:space-around;align-items:center}.inputTimer{display:block;width:90px;font-weight:bold;border:none;outline:none;background:none;pointer-events:none}.IncremDecremBtn{display:block;border:none;background:none;cursor:pointer}.IncremDecremBtn:hover path{stroke-opacity:1}.coloredCircle{margin:0;padding:0;width:50px;height:50px;font-size:20px;border-radius:50%;background-color:#000;border:none;outline:none;background:none;cursor:pointer}.fontSettingCircle::after{content:"Aa"}.hoverSettingCircle:hover{box-shadow:0 0 0 3px #fff,0 0 0 5px #eff1fa}.selectedFontCircle{background-color:#161932 !important;color:#fff}.selectedColorCircle::after{content:"✓"}#applySettingsButton{margin-top:auto;margin-bottom:-15px;width:125px;height:45px;border-radius:30px;color:#fff;background-color:var(--theme-color);align-self:center;border:none;outline:none;cursor:pointer}#applySettingsButton:hover{filter:brightness(125%)}#settingsButton{display:block;text-align:center;margin-top:25px;border:none;outline:none;background:none;cursor:pointer}#settingsButton svg:hover path{opacity:1}body{--font-family: "Kumbh Sans";--theme-color: #F87070}*{margin:0;padding:0}body{font-family:var(--font-family);background-color:#1e213f}#root{display:flex;flex-direction:column;align-items:center}header{margin-top:35px}nav{display:flex;margin-top:30px;padding:7px 5px 7px 5px;border-radius:40px;background-color:#161932}@media only screen and (max-width: 400px){nav{width:340px}}.navItem{display:flex;width:120px;height:50px;font-weight:bold;background-color:transparent;color:#636984;justify-content:center;align-items:center;text-decoration:none;border:none;outline:none;cursor:pointer}.navItem:hover{color:#d7e0ff}.navItemActive{border-radius:40px;background-color:var(--theme-color);color:#161932}#progressbarBox{width:300px;height:300px;margin-top:45px;border-radius:50%;border:14px solid #1a1e3a;box-shadow:-15px -15px 200px -100px #fff,40px 40px 100px -30px #000}.startButton{height:30px;position:relative;top:80px;padding-left:12px;font-size:16px;font-weight:bold;letter-spacing:12px;text-align:center;background:none;color:#d7e0ff;border:none;outline:none;cursor:pointer}.startButton:hover{color:#f87070}